"use client";

import { useEffect, useState } from "react";

export default function CalendarPage() {
  const [events, setEvents] = useState([]);

  useEffect(() => {
    const fetchEvents = async () => {
      try {
        const res = await fetch("http://localhost:5000/api/calendar/events");
        const data = await res.json();
        setEvents(data);
      } catch (error) {
        console.error("Failed to fetch events:", error);
      }
    };

    fetchEvents();
  }, []);

  return (
    <div className="p-6">
      <h1 className="text-2xl font-bold mb-4">Your Calendar</h1>
      <ul className="space-y-4">
        {events.length === 0 ? (
          <p>No upcoming events found.</p>
        ) : (
          events.map((event) => (
            <li key={event.id} className="p-4 border rounded shadow">
              <p className="font-semibold">{event.summary}</p>
              <p className="text-sm text-gray-600">
                {event.start.dateTime || event.start.date} â†’{" "}
                {event.end.dateTime || event.end.date}
              </p>
              {event.location && (
                <p className="text-sm text-gray-500">{event.location}</p>
              )}
            </li>
          ))
        )}
      </ul>
    </div>
  );
}
